.navContent.desktop {
  background-color: $white;
  width: 100%;
  position: sticky;
  top: 0;
  #navBag {
    padding-right: 1rem;
    display: flex;
    align-items: center;
    transform: translateY(-3px);
    .label {
      transform: translateY(3px);
    }
    #bagCount {
      top: 4px;
      right: 0;
      left: initial;
    }
  }
  .wrapper {
    flex-grow: 1;
    background-color: $white;
    padding: 0 2rem;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #e5e5e5;
    position: sticky;
    top: 0;
    height: 4rem;
    z-index: 9999999;
  }
  z-index: 9999999;
  &.headroom--unpinned {
    transform: translate(0, -64px);
  }
  .buySticky {
    height: 7rem;
    width: 100%;
    background-color: $white;
    border-bottom: 1px solid #e5e5e5;
    display: flex;
    margin-top: -7rem;
    z-index: 99999;
    transition: transform $longDuration $ease, opacity 120ms 120ms $ease;
    transform: translate(0, 0);
    padding: 0 2rem;
    align-items: center;
    justify-content: space-between;
    opacity: 0;
    .left {
      display: flex;
      align-items: center;
      .img {
        width: 5rem;
        margin-right: 1rem;
        img {
          width: 100%;
        }
      }
    }
    .left,
    .right {
      transition: all $longDuration $duration $ease;
      transform: translate(0, -2rem);
      opacity: 0;
    }
    .productName {
      font-size: 2rem;
      font-weight: 700;
    }
    .price {
      font-size: 1.5rem;
    }
    &.visible {
      opacity: 1;
      transform: translate(0, 7rem);
      .left,
      .right {
        transform: translate(0, 0);
        opacity: 1;
      }
    }
  }
  .brand {
    width: 100px;
    text-align: center;
    img {
      width: 60px;
      margin: 0 auto;
      transform: translate(0, -1rem);
      @include transition(all,
			$longDuration);
    }
  }
  &.reduced {
    .brand {
      img {
        width: 40px;
        transform: translate(0, 0);
      }
    }
  }
  .tools,
  .mainLinks {
    display: flex;
    width: 400px;
  }
  .mainLinks {
    font-size: 1.125rem;
    li:not(:last-child) {
      margin-right: 2.5rem;
    }
  }
  .tools {
    flex-direction: row-reverse;
    li {
      margin-left: 2.5rem;
      font-size: 0.875rem;
      display: flex;
      align-items: center;
      img {
        margin-right: 0.5rem;
      }
    }
  }
}

