body.desktop {
  &:before {
    position: fixed;
    width: 100vw;
    height: 100vh;
    content: '';
    z-index: 99999999;
    opacity: 0;
    pointer-events: none;
    @include transition(opacity);
    background-color: rgba(0, 0, 0, 0.6);
  }
  &:after {
    position: fixed;
    width: 100vw;
    height: 100vh;
    content: '';
    top: 0;
    left: 0;
    z-index: 999999;
    opacity: 0;
    pointer-events: none;
    @include transition(opacity);
    background-color: rgba(0, 0, 0, 0.6);
  }
}

body.desktop.overlayed {
  &:after {
    opacity: 1;
  }
}

body.desktop.fixed {
  &:before {
    opacity: 1;
  }
}

