    }
    .block {
      padding: 2rem;
      width: 25vw;
      border-left: 1px solid #e5e5e5;
      background-position: bottom left;
      background-size: 100%;
      background-repeat: no-repeat;
      &.padded {
        background-position: 2rem calc(100% - 2rem);
        background-size: calc(100% - 4rem);
      }
      &.double {
        width: 50vw;
      }
      &.full {
        background-position: top left;
        background-size: 100%;
      }
      &.unbordered {
        border-color: transparent;
      }
    }
    &.active {
      pointer-events: initial;
      transform: translateY(0);
      opacity: 1;
    }
  }
}

.right .sizeOptions {
  display: inline-block;
  height: 3.75rem;
  line-height: 3.75rem;
  vertical-align: top;
  margin-right: 1.5rem;
  .truefit {
    margin-right: 1rem;
  }
  .truefit::before {
    top: 50%;
    transform: translateY(-50%);
  }
}

.topVertical{
  padding: 0;
  margin: 1rem 0 0 0;
  list-style-type: none;
  li{
    display: flex;
    align-items: center;
    cursor: pointer;
    &:not(:last-child){border-bottom: 1px solid #e5e5e5}
    .img{
      width: 7rem;
      img{
        width: 100%;
      }
    }
    span{
      display: block;
    }
    .details{
      font-size: .875rem;
      margin-left: 1rem;
    }
    .num {
      margin-right: 1rem;
      position: relative;
      width: 1.5rem;
      height: 1.5rem;
      line-height: 1.5rem;
      text-align: center;
      font-size: .875rem;
      color: white;
      z-index: 99;
      &:before {
        position: absolute;
        top: 0rem;
        left: 0rem;
        width: 1.5rem;
        height: 1.5rem;
        // border: 1px solid black;
        background-color: black;
        content: '';
        z-index: -1;
        transform: rotate(0.125001turn);
        transform-origin: center center;
      }
    }
    &:hover{
      .details{
        text-decoration: underline;
        color: $main-color;
      }
    }
  }
}

.promo.filters{
  padding: 3rem 3rem;
  position: relative;
  &:before{
    content: "";
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    background: $main-gradient;
    width: calc(100% - 3rem);
    height: calc(100% - 3rem);
  }
  h6{
    color: white;
    position: relative;
  }
  ul{
    list-style-type: none;
    margin: 1.5rem 0 0 0;
    padding: 0;
    li{
      display: table;
      height: 3.75rem;
      padding:  0 1.5rem;
      line-height: 3.75rem;
      &:not(:last-child){
        margin-bottom: 0.5rem;
      }
      border-radius: 3rem;
      box-shadow: inset 0 0 0 1px white;
      color: white;
      font-size: 1.5rem;
      @include transition(all);
      cursor: pointer;
      position: relative;
      overflow: hidden;
      z-index: 1;
      
      &:hover{
        background-color: white;
        color: $main-color;
      }
    }
  }
}

.promo.bigOff{
  padding: 1.5rem;
  display: flex;
  position: relative;
  flex-direction: column;
  cursor: pointer;
  .content{
    padding: 1rem;
    position: relative;
    background: linear-gradient(135deg, $alt-color, $alt2-color);
    color: white;
    font-weight: 600;
    flex-grow: 1;
    @include transition(all);
    background-size: 100%;
    background-position: bottom right;
    .left{
      position: absolute;
      top: 1rem;
      left: 1rem;
    }
    .right{
      position: absolute;
      top: 1rem;
      right: 1rem;
    }
    .sale{
      position: absolute;
      bottom: 1rem;
      left: 1rem;
      font-size: 2.875rem;
      line-height: 2.8rem
    }
  }
  .btn{
    margin-top: 1rem;
    border: 2px solid $main-color;
    font-weight: 600;
    @include transition(all)
  }
  &:before{
    border: 0px solid $alt2-color;
    position: absolute;
    width: 100%;
    height: 100%;
    content: "";
    top: 0;
    left: 0;
    box-sizing: border-box;
    opacity: 0;
    @include transition(all)
  }
  &:hover{
    // &:before{
    //   border-width: 3px;
    //   opacity: 1;
    // }
    .content{
      background-size: 200%;
    }
    .btn{
      border-color: $alt2-color;
      color: $alt2-color;
    }
  }
}

.promo.shipping{
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
  &:before{
    border: 0px solid $alt2-color;
    position: absolute;
    width: 100%;
    height: 100%;
    content: "";
    top: 0;
    left: 0;
    box-sizing: border-box;
    opacity: 0;
    @include transition(all)
  }
  p{
    font-size: 2.875rem;
    line-height: 2.8rem;
    color: $main-color;
    font-weight: 600;
    padding: 1.5rem;
    @include transition(all)
  }
  .triangle{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 25vw;
    height: 25vw;
    padding: 0;
    background: linear-gradient(135deg, $alt-color, $alt2-color);
    background-position: bottom right;
    background-size: 100%;
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
    @include transition(all, $duration);
  }
  &:hover{
    &:before{
      border-width: 3px;
      opacity: 1;
    }
    p{
      color: $alt2-color;
    }
    .triangle{
      background-size: 200%;
      padding: 2rem;
      width: calc(25vw - 3rem);
      height: calc(25vw - 3rem);
      bottom: 1.5rem;
      right: 1.5rem;
    }
  }
}

body.me2we .grid{
  h2{
    padding: 0!important;
    margin-bottom: 1rem!important;
    margin-top: 2rem !important;
    color: #0B3252;
    font-size: 2.25rem;
  }
  p{
    font-size: 1rem;
    line-height: 1.2;
    color: #767676;
    margin-bottom:1rem !important;
  }
  .icon{
    margin: 0;
    padding: 0;
    width: 3.75rem;
    img{
      width: 100%;
    }
  }
}
h1 + h6{
  margin-top: 1rem;
}
.filterPanel{
  position: absolute;
  transform: translateY(-100%);
  opacity: 0;
  background-color: white;
  @include transition(all, $longDuration);
  header{
    position: relative;
    padding-left: 2rem;
    .close{
      cursor: pointer;
      position: absolute;
      top: 2rem;
      right: 2rem;
      display: flex;
      align-items: center;
      font-size: 0.875rem;
      font-weight: 600;
      svg{
        margin-left: .5rem;
        width: 1em;
      }
    }
  }
  img.full{
    width: 100%;
  }
  &.active{
    transform: translateY(0);
    opacity: 1;
  }
}
